<template>
  <section class="masters-block">
    <div class="container">
      <img src="/static/img/home/6/1.png" alt="" class="brush">
      <div class="top flex-between align-center">
        <div class="title">Наши мастера регулярно <br>повышают свою квалификацию</div>
        <div v-if="items.length > 3" class="flex-start align-center">
          <div class="prev carousel-btn" @click="prevMaster">
            <svg width="8" height="12" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M6.32959 1.30876L1.66292 5.97542L6.32959 10.6421" stroke="#22252C" stroke-width="1.5"
                    stroke-linecap="round" stroke-linejoin="round"/>
            </svg>
          </div>
          <div class="next carousel-btn" @click="nextMaster">
            <svg width="8" height="12" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M1.6626 1.30876L6.32926 5.97542L1.6626 10.6421" stroke="#22252C" stroke-width="1.5"
                    stroke-linecap="round" stroke-linejoin="round"/>
            </svg>
          </div>
        </div>
      </div>
      <VueSlickCarousel v-if="!loading" v-bind="mastersSettings" ref="masters">
        <MasterCard v-for="master in items" :key="master.id" :master="master"></MasterCard>
      </VueSlickCarousel>
    </div>
  </section>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import MasterCard from "@/components/Masters/MasterCard";
import api from "@/api"

export default {
  name: "index",
  components: {VueSlickCarousel, MasterCard},
  props: ['category'],
  methods: {
    nextMaster() {
      this.$refs.masters.next()
    },
    prevMaster() {
      this.$refs.masters.prev()
    },
    async getItems() {
      this.loading = true
      this.items = await api.getMasters(this.category)
      this.loading = false
    }
  },
  created() {
    this.getItems()
  },
  data: () => ({
    loading: false,
    items: [],
    mastersSettings: {
      slidesToShow: 3,
      arrows: false,
      dots: false,
      touchMove: false,
      variableWidth: true,
      adaptiveHeight: true,
      responsive: [
        {
          breakpoint: 1170,
          settings: {
            slidesToShow: 1,
            swipe: true,
            touchMove: true,
            draggable: true,
            centerMode: true
          }
        }
      ]
    },
  })
}
</script>

