<template>
  <section class="cases-block">
    <div class="container">
      <h2 class="section__title">Посмотрите наши работы</h2>
      <img src="/static/img/home/4/bg.png" alt="" class="leaf">
      <VueSlickCarousel
          v-if="this.$vssWidth <= 1170 && !loading"
          class="cases-slider"
          :arrows="false"
          :dots="false"
          :draggable="true"
          :swipe="true"
          :variableWidth="true"
          :adaptiveHeight="true"
      >
        <div class="case-img" v-for="item in items" :key="item.id">
          <img :src="getSrc(item.thumb)" alt="">
        </div>
      </VueSlickCarousel>


      <div v-if="this.$vssWidth > 1170" class="columns">
        <div
            v-for="(item, index) in casesFirstPart"
            :class="[
                'case-img',
                {'tall': (index === 1 || index === 7) && items.length > 9}
            ]"
            :key="item.id"
        >
          <img :src="getSrc(item.thumb)" alt="">
        </div>
        <div @click="showAll = true" v-if="!showAll && items.length > 9" class="show-button flex-center align-center">
          <span>Смотреть все работы</span>
          <div class="wrap">
            <svg width="12" height="10" viewBox="0 0 12 10" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M11.168 4.81706L1.16797 4.81706" stroke="#22252C" stroke-width="1.5" stroke-linecap="round"
                    stroke-linejoin="round"/>
              <path d="M7.13477 0.800829L11.1681 4.81683L7.13477 8.8335" stroke="#22252C" stroke-width="1.5"
                    stroke-linecap="round" stroke-linejoin="round"/>
            </svg>
          </div>
        </div>
      </div>
      <div class="columns mt-0" v-if="showAll && items.length > 10">
        <div
            v-for="(item, index) in casesSecondPart"
            :class="[
                'case-img',
                {'tall': (index === 1 || index === 7) && casesSecondPart.length > 9}
            ]"
            :key="item.id"
        >
          <img :src="getSrc(item.thumb)" alt="">
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import api from "@/api"
import {createImageUrl} from "@/utils";

export default {
  name: "Cases",
  components: {VueSlickCarousel},
  props: ['category'],
  computed: {
    casesFirstPart() {
      return this.showAll ? this.items.slice(0, 10) : this.items.slice(0, 9)
    },
    casesSecondPart() {
      return this.showAll ? this.items.slice(10) : []
    }
  },
  data: () => ({
    showAll: false,
    items: [],
    loading: false
  }),
  methods: {
    async getItems() {
      this.loading = true
      this.items = await api.getCases(this.category)
      this.loading = false
    },
    getSrc(src) {
      return createImageUrl(src)
    }
  },
  created() {
    this.getItems()
  }
}
</script>

