<template>
  <section class="reviews-block">
    <img v-if="this.$vssWidth > 1170" src="/static/img/home/7/bg.png" alt="" class="bg">
    <img v-if="this.$vssWidth <= 1170" src="/static/img/home/3/bg-m.png" alt="" class="bg">
    <div class="container">
      <div class="top flex-start align-start">
        <div class="left">
          <div class="title">Отзывы наших <br>любимых <br>клиентов</div>
          <div class="flex-start align-center">
            <div class="prev carousel-btn" @click="prevReview">
              <svg width="8" height="12" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M6.32959 1.30876L1.66292 5.97542L6.32959 10.6421" stroke="#22252C" stroke-width="1.5"
                      stroke-linecap="round" stroke-linejoin="round"/>
              </svg>
            </div>
            <div class="next carousel-btn" @click="nextReview">
              <svg width="8" height="12" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M1.6626 1.30876L6.32926 5.97542L1.6626 10.6421" stroke="#22252C" stroke-width="1.5"
                      stroke-linecap="round" stroke-linejoin="round"/>
              </svg>
            </div>
          </div>
        </div>
        <div class="center">
          <img v-if="this.$vssWidth > 1170" src="/static/img/home/7/1.png" alt="" class="phone">
          <img v-if="this.$vssWidth <= 1170" src="/static/img/home/7/1-m.png" alt="" class="phone">
          <VueSlickCarousel v-if="!loading" v-bind="reviews1" ref="reviews1" class="reviews-slider1">
            <div class="img" v-for="review in imageReviews" :key="review.id">
              <img :src="getSrc(review.thumb)" alt="">
            </div>
          </VueSlickCarousel>
        </div>
        <div class="right" v-if="this.$vssWidth > 1170 && imageReviews.length > 4">
          <VueSlickCarousel v-if="!loading" v-bind="reviews2" ref="reviews2" class="reviews-slider2">
            <div class="img" v-for="review in backgroundReviews" :key="review.id">
              <img :src="getSrc(review.thumb)" alt="">
            </div>
          </VueSlickCarousel>
        </div>
      </div>
      <div class="bottom flex-between align-start">
        <div class="col">
          <VideoReview v-for="review in col1Reviews" :key="review.id" :review="review"></VideoReview>
          <RouterLink to="/reviews">
            <div class="show-button flex-center align-center">
              <span>Смотреть все отзывы</span>
              <div class="wrap">
                <svg width="12" height="10" viewBox="0 0 12 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M11.168 4.81706L1.16797 4.81706" stroke="#22252C" stroke-width="1.5" stroke-linecap="round"
                        stroke-linejoin="round"/>
                  <path d="M7.13477 0.800829L11.1681 4.81683L7.13477 8.8335" stroke="#22252C" stroke-width="1.5"
                        stroke-linecap="round" stroke-linejoin="round"/>
                </svg>
              </div>
            </div>
          </RouterLink>
        </div>
        <div class="col" v-if="this.$vssWidth > 1170">
          <div class="title">Видеоотзывы <br>клиентов</div>
          <VideoReview v-for="review in col2Reviews" :key="review.id" :review="review"></VideoReview>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import VideoReview from "@/components/Reviews/VideoReview";
import api from "@/api"
import {createImageUrl} from "@/utils";

export default {
  name: "Reviews",
  components: {VueSlickCarousel, VideoReview},
  created() {
    this.getItems()
  },
  props: ['category'],
  methods: {
    getSrc(src) {
      return createImageUrl(src)
    },
    async getItems() {
      this.loading = true
      const data = await api.getReviews(this.category)
      this.imageReviews = data.image_reviews
      this.videoReviews = data.video_reviews
      this.loading = false
    },
    nextReview() {
      this.$refs.reviews1.next()
      this.$refs.reviews2.next()
    },
    prevReview() {
      this.$refs.reviews1.prev()
      this.$refs.reviews2.prev()
    },
  },
  computed: {
    col1Reviews: function () {
      return this.videoReviews.slice(0, 2)
    },
    col2Reviews: function () {
      return this.videoReviews.slice(2, 4)
    },
    backgroundReviews: function () {
      const res = this.imageReviews.concat()
      const first = res.shift()
      res.push(first)
      return res
    }
  },
  data: () => ({
    imageReviews: [],
    videoReviews: [],
    loading: false,
    reviews1: {
      slidesToShow: 1,
      arrows: false,
      dots: false,
      touchMove: false,
      variableWidth: true,
      adaptiveHeight: true
    },
    reviews2: {
      slidesToShow: 4,
      arrows: false,
      dots: false,
      touchMove: false,
      variableWidth: true,
      adaptiveHeight: true
    },
  })
}
</script>

<style scoped>

</style>